import React from 'react';
import Layout from '../layout/Layout';
import PostCard from '../components/post';
import { Helmet } from 'react-helmet';
import { BlogGetById } from "../services/BlogService";

export default function Blog() {
  const [blogData, setBlogData] = React.useState('');
  BlogGetById("20210312064310").then(response => setBlogData(response[0]));
  return (
    <Layout>      
        <PostCard
          author="BR"
          title={blogData.title}
          timestamp={new Date(blogData.timestamp).toLocaleString()}
          imageLink={blogData.imageUrl}
          imageTitle={blogData.title}
          content={blogData.content}
          extendedContent={blogData.extendedContent}
        >
        </PostCard>      
      <Helmet>
        <title>Blogs | Bipul Raman</title>
      </Helmet>
    </Layout>
  );
}